 * {
	 padding: 0;
	 margin: 0;
	 box-sizing: border-box;
	 font-family: "Roboto", sans-serif;
}
 body {
	 background: #3d3d53;
	 font-family: "Roboto", sans-serif;
}
/* #18181e */
 .logo {
	 display: flex;
	 justify-content: center;
	 align-items: center;
	 margin: 0 16px;
}
 ul {
	 list-style: none;
	 display: flex;
	 justify-content: center;
	 align-items: center;
	 flex-direction: row;
	 list-style: none;
}
 ul li Link {
	 height: 60px;
	 margin: 0 16px;
	 display: flex;
	 justify-content: center;
	 align-items: center;
	 text-decoration: none;
	 color: #707184;
	 text-transform: capitalize;
	 position: relative;
}
 ul li Link:hover {
	 color: #ececec;
}
 ul li Link:hover::after {
	 content: "";
	 position: absolute;
	 width: 100%;
	 bottom: 0;
	 border: 1px solid #9466ff;
}
 .active {
	 color: #f3f3f3 !important;
}
 .active::after {
	 content: "";
	 position: absolute;
	 width: 100%;
	 bottom: 0;
	 border: 1px solid #9466ff;
}
 .account {
	 color: #fff;
	 cursor: pointer;
}
 .account a {
	 font-size: 12px;
	 text-transform: uppercase;
	 font-weight: bold;
}
 .register {
	 padding: 12px 19px;
	 background: none;
	 border: none;
	 color: #fff;
	 cursor: pointer;
}
 .login {
	 padding: 12px 23px;
	 background: #9466ff;
	 border: none;
	 color: #fff;
	 border-radius: 4px;
}
 nav {
	 height: 100%;
	 display: flex;
	 justify-content: space-between;
	 align-items: center;

}
 .mainbar {
	 display: flex;
	 justify-content: center;
	 align-items: center;
}
 
 .nav-5 {
